import { LayoutComponent } from './layouts/layout/layout.component';
import { VexRoutes } from '@vex/interfaces/vex-route.interface';

export const appRoutes: VexRoutes = [
  {
    path: 'login',
    loadComponent: () =>
      import('./pages/pages/auth/login/login.component').then(
        (m) => m.LoginComponent
      )
  },
  {
    path: '',
    component: LayoutComponent,
    children: [
      {
        path: 'mesa-control',
        loadComponent: () =>
          import('./pages/mesa-control/mesa-control.component').then(
            (m) => m.MesaControlComponent
          )
      },
      {
        path: 'status',
        loadComponent: () =>
          import('./pages/status/status.component').then(
            (m) => m.StatusComponent
          )
      },
      {
        path: 'fases/integracion',
        loadComponent: () =>
          import('./pages/fases/integracion/integracion.component').then(
            (m) => m.IntegracionComponent
          )
      },
      {
        path: 'fases/liquidacion',
        loadComponent: () =>
          import('./pages/fases/liquidacion/liquidacion.component').then(
            (m) => m.LiquidacionComponent
          )
      },
      {
        path: 'fases/liberacion',
        loadComponent: () =>
          import('./pages/fases/liberacion/liberacion.component').then(
            (m) => m.LiberacionComponent
          )
      },
      {
        path: 'configuracion/catalogos/agencias',
        loadComponent: () =>
          import('./pages/configuracion/catalogos/agencias/agencias.component').then(
            (m) => m.AgenciasComponent
          )
      }
    ]
  }
];
