import { Injectable } from '@angular/core';
import { VexLayoutService } from '@vex/services/vex-layout.service';
import { NavigationItem } from './navigation-item.interface';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class NavigationLoaderService {
  private readonly _items: BehaviorSubject<NavigationItem[]> =
    new BehaviorSubject<NavigationItem[]>([]);

  get items$(): Observable<NavigationItem[]> {
    return this._items.asObservable();
  }

  constructor(private readonly layoutService: VexLayoutService) {
    this.loadNavigation();
  }

  loadNavigation(): void {
    this._items.next([
      {
        type: 'subheading',
        label: 'Dashboards',
        children: [
          {
            type: 'link',
            label: 'Analytics',
            route: '/',
            icon: 'mat:insights',
            routerLinkActiveOptions: { exact: true }
          }
        ]
      },
      {
        type: 'subheading',
        label: 'Expediente nico',
        children: [
          {
            type: 'dropdown',
            label: 'Fases',
            icon: 'mat:timeline',
            children: [
              {
                type: 'link',
                label: 'Integracin',
                route: '/fases/integracion',
                icon: 'mat:add_circle'
              },
              {
                type: 'link',
                label: 'Liquidacin',
                route: '/fases/liquidacion',
                icon: 'mat:account_balance'
              },
              {
                type: 'link',
                label: 'Liberacin',
                route: '/fases/liberacion',
                icon: 'mat:check_circle'
              }
            ]
          }
        ]
      },
      {
        type: 'subheading',
        label: 'Control y Gestin',
        children: [
          {
            type: 'link',
            label: 'Mesa de Control',
            route: '/mesa-control',
            icon: 'mat:dashboard_customize'
          },
          {
            type: 'link',
            label: 'Status',
            route: '/status',
            icon: 'mat:monitor_heart'
          }
        ]
      },
      {
        type: 'subheading',
        label: 'Configuracin',
        children: [
          {
            type: 'dropdown',
            label: 'Catlogos',
            icon: 'mat:category',
            children: [
              {
                type: 'link',
                label: 'Agencias',
                route: '/configuracion/catalogos/agencias',
                icon: 'mat:business'
              }
            ]
          }
        ]
      }
    ]);
  }
}
